    <div class="text-right mt-4">
      <button class="btn btn-primary" @click=" openModal('set')">å»ºç«åªæ å¸</button>
    </div>
    <table class="table mt-4">
      <thead>
        <tr>
          <th>åªæ åç¨±</th>
          <th>åç¨</th>
          <th>ææ£</th>
          <th>æªæ­¢æ¥æ</th>
          <th>åªæ ç¢¼</th>
          <th>ç·¨è¼¯</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(item) in coupons" :key="item.id">
          <td>{{item.title}}</td>
          <td>
            <span class="text-success" v-if="item._is_enable">åç¨</span>
            <span class="text-success" v-else>æªåç¨</span>
          </td>
          <td>{{item.percent}} %</td>
          <td>{{item.due_date | date}}</td>
          <td>{{item.code}}</td>
          <td>
            <button class="btn btn-outline-primary btn-sm mr-1" @click=" openModal('edit',item)">ç·¨è¼¯</button>
            <button class="btn btn-outline-danger btn-sm" @click="openModal('delete',item)">åªé¤</button>
          </td>
        </tr>
      </tbody>
    </table>
    <!-- pagination -->
    <Pagination :pagination="pagination" @getpage="getCoupons" v-if="pagination"></Pagination>
    <!-- Modal-edit,set -->
    <div
      class="modal fade"
      id="couponModal"
      tabindex="-1"
      role="dialog"
      aria-labelledby="couponModalLabel"
      aria-hidden="true"
    >
      <div class="modal-dialog modal-sm" role="document">
        <div class="modal-content border-0">
          <div class="modal-header bg-dark text-white">
            <h5 class="modal-title" id="couponModalLabel">
              <span v-if="isNew === true">æ°å¢åªæ å¸</span>
              <span v-else>ç·¨è¼¯åªæ å¸</span>
            </h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span class="text-white" aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <div class="row">
              <div class="col">
                <div class="form-group">
                  <label for="couponTitle">åªæ å¸åç¨±</label>
                  <input
                    type="text"
                    class="form-control"
                    id="couponTitle"
                    placeholder="è«è¼¸å¥åªæ å¸"
                    v-model="tempCoupon.title"
                  />
                </div>

                <div class="form-group">
                  <label for="discount">ææ£</label>
                  <input
                    type="number"
                    class="form-control"
                    id="discount"
                    placeholder="è«è¼¸å¥ææ£%æ¸"
                    v-model="tempCoupon.percent"
                  />
                </div>
                <div class="form-group">
                  <label for="due_date">æªæ­¢æ¥æ</label>
                  <input type="date" class="form-control" id="due_date" v-model="due_date" />
                </div>
                <div class="form-group">
                  <label for="code">åªæ ç¢¼</label>
                  <input
                    type="text"
                    class="form-control"
                    id="code"
                    placeholder="è«è¼¸å¥åªæ ç¢¼"
                    v-model="tempCoupon.code"
                  />
                </div>
                <hr />
                <div class="form-check">
                  <input
                    type="checkbox"
                    class="form-check-input"
                    id="is_enable"
                    placeholder="æ¯å¦åç¨"
                    v-model="tempCoupon._is_enable"
                  />
                  <label
                    for="is_enable"
                    class="form-check-label text-success"
                    v-if="tempCoupon._is_enable"
                  >å·²åç¨</label>
                  <label for="is_enable" class="form-check-label" v-else>æªåç¨</label>
                </div>
              </div>
            </div>
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-outline-secondary" data-dismiss="modal">åæ¶</button>
            <button type="button" class="btn btn-primary" @click="updateCoupon">ç¢ºèª</button>
          </div>
        </div>
      </div>
    </div>
    <!-- Moadal-delete -->
    <div
      class="modal fade"
      id="delCouponModal"
      tabindex="-1"
      role="dialog"
      aria-labelledby="exampleModalLabel"
      aria-hidden="true"
    >
      <div class="modal-dialog" role="document">
        <div class="modal-content border-0">
          <div class="modal-header bg-danger text-white">
            <h5 class="modal-title" id="exampleModalLabel">
              <span>åªé¤åªæ å¸</span>
            </h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            æ¯å¦åªé¤
            <strong class="text-danger">{{ tempCoupon.title }}</strong> åªæ å¸(åªé¤å¾å°ç¡æ³æ¢å¾©)ã
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-outline-secondary" data-dismiss="modal">åæ¶</button>
            <button type="button" class="btn btn-danger" @click="updateCoupon">ç¢ºèªåªé¤</button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import $ from 'jquery'
import Pagination from '@/components/common/Pagination'
export default {
  data () {
    return {
      coupons: [],
      tempCoupon: {
      },
      due_date: new Date(),
      isNew: false,
      openModalMethod: '',
      isLoading: false,
      pagination: {}

    }
  },
  components: {
    Pagination
  },
  methods: {
    getCoupons (page = 1) {
      const vm = this
      const api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/admin/coupons?page=${page}`
      vm.isLoading = true
      this.$http.get(api).then(res => {
        vm.coupons = res.data.coupons
        vm.pagination = res.data.pagination
        console.log(res.data.coupons.due_date)
        vm.isLoading = false
      })
    },
    openModal (openModalMethod, item) {
      if (openModalMethod === 'set') {
        this.tempCoupon = {}
        this.isNew = true
        this.openModalMethod = 'set'
        $('#couponModal').modal('show')
      } else if (openModalMethod === 'edit') {
        this.tempCoupon = Object.assign({}, item)
        this.isNew = false
        this.openModalMethod = 'edit'
        $('#couponModal').modal('show')
      } else if (openModalMethod === 'delete') {
        this.tempCoupon = item
        this.isNew = false
        this.openModalMethod = 'delete'
        $('#delCouponModal').modal('show')
      }
    },
    updateCoupon () {
      const vm = this
      // é è¨­çºpost
      let api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/admin/coupon`
      let httpMethod = 'post'
      if (vm.openModalMethod === 'edit') {
        api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/admin/coupon/${vm.tempCoupon.id}`
        httpMethod = 'put'
      } else if (vm.openModalMethod === 'delete') {
        api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/admin/coupon/${vm.tempCoupon.id}`
        httpMethod = 'delete'
      }
      this.$http[httpMethod](api, { data: vm.tempCoupon }).then(res => {
        const modal = vm.openModalMethod === 'delete' ? '#delCouponModal' : '#couponModal'
        if (res.data.success) {
          $(modal).modal('hide')
          vm.getCoupons()
        } else {
          $(modal).modal('hide')
          vm.getCoupons()
        }
      })
    }
  },
  created () {
    this.getCoupons()
  },
  watch: {
    due_date () {
      const vm = this
      const timestamp = Math.floor(new Date(vm.due_date) / 1000)
      vm.tempCoupon.due_date = timestamp
    }
  }

}
</script>
